* {
  margin: 0;
  padding: 0;
}

html{
  font-size: 16px;

  @media (max-width: $large) {
    font-size: 14px;
  }
  
  @media (max-width: $medium) {
    font-size: 12px;
  }
  @media (max-width: $small) {
    font-size: 12px;
  }
}

body {
  box-sizing: border-box;
  background: gray;
  width: 100vw;
}
