
.mapContainer {
  max-width: 375px;
  max-height: 375px;
  width: 375px;;
  height: 375px;

  @media (max-width: $large) {
    width: 80vw;
    height: 80vw;
    margin-top: 2rem;
  }

  @media (max-width: $small) {
    width: calc(100vw-1rem);
    height: calc(100vw-1rem);
    margin-left: auto;
    margin-right: auto;
  }
}

.map {
  border-radius: 30px;
}